.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.LightGrayB{
  background-color: lightgray!important;
}

.FullWidth{
  min-width: 100%;
}

.headerCellStyle1.MuiColumnHeader_id {
  text-align: center;
}
.Padding25px {
  padding: 25px;
}

.Spacing {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.headerStyle1 {
  /* background-color: rgb(67, 67, 221); */
  border-top: 3px solid rgb(67, 67, 221);
  border-bottom: 3px solid rgb(67, 67, 221);
}

.headerCellStyle1 {
  color: black;
  font-weight: 600;
  /* height: 70px; */
}
.bodyCellStyle1 {
  padding-top: 5px;
  padding-bottom: 5px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.row {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: row;
  width: 100%;
}

.column {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  margin-left: 2%;
  width:100%;
  height: 100%;

}

.centerAlign {
  display: flex;
  align-items: center;
  justify-content: center;
}

tbody tr:nth-child(even) {
  background-color: #cccccc;
  border-color: #cccccc;
}

table {
  padding-top: 50px;
  padding-bottom: 50px;

  background-color: #f2f2f2;
}

td {
  padding: 15px 20px 15px 20px;
  margin: 0px;
}

th {
  background-color: #1a1a1a;
  color: white;
  padding: 15px 20px 15px 20px;
}

table {
  border-collapse: collapse;
}


.flexStart{
  display: flex;
  justify-content: flex-start;
}
/* Ripple effect */
.ripple {
  background-position: center;
  transition: background 0.6s;
}
.ripple:hover {
  background: #bbbbbb radial-gradient(circle, transparent 1%, #bbbbbb 1%)
    center/15000%;
}
.ripple:active {
  background-color: #bbbbbb;
  background-size: 100%;
  transition: background 0s;
}

.icon {
  width: 30px;
}

.angle_up {
  transform: rotate(180deg);
  animation: rotationUp 0.3s linear;
}

.angle_down {
  /* transform: rotate(0deg); */
  animation: rotationDown 0.3s linear;
}

.dd_rec_list_header {
  min-width: 24px;
}

@keyframes rotationUp {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(180deg);
  }
}

@keyframes rotationDown {
  from {
    transform: rotateX(180deg);
  }
  to {
    transform: rotateX(0deg);
  }
}

.ql-editor {
  font-size: large;
  min-height: 250px;
}

